/**
 * usage
 * 1.  import api from 'path/to/api'
 * 2.  Vue.use(api)
 * 3.  this.$wGet/this.$wPost
 * 4.  Vue.wGet/Vue.wPost
 */

import router from '../../router'
import ElementUI from 'element-ui'
import { assign } from 'lodash'

let plugin = {}
plugin.install = Vue => {
  /**
   * http GET
   * @param {String} uri 
   * @param {Object} query 
   * @param {Function} prev 
   */
  Vue.prototype.$wGet = (uri, params = {}, prev = data => data) => {
    params = assign({}, {
      customId: router.currentRoute.query.id,
      _: Number(new Date())
    }, params)
    return new Promise((resolve, reject) => {
      Vue.http.get(uri, { params: params }, {
        emulateJSON: true
      }).then(response => {
        if (typeof response.body === 'string') {
          response.body = JSON.parse(response.body)
        }
        response.body = prev(response.body)
        if (verify(response.body)) {
          resolve(response.body)
        }
      }, response => {
        error(response)
        reject(response)
      })
    })
  }

  /**
   * http POST
   * @param {String} uri 
   * @param {Object} params 
   * @param {Function} prev 
   */
  Vue.prototype.$wPost = (uri, params = {}, prev = data => data) => {
    params = assign({}, {
      customId: router.currentRoute.query.id,
      _: Number(new Date())
    }, params)
    return new Promise((resolve, reject) => {
      Vue.http.post(uri, params, {
        emulateJSON: true,
        emulateHTTP: true
      }).then(response => {
        if (typeof response.body === 'string') {
          response.body = JSON.parse(response.body)
        }
        response.body = prev(response.body)
        if (verify(response.body)) {
          resolve(response.body)
        }
      }, response => {
        error(response)
        reject(response)
      })
    })
  }

  /**
   * http POST
   * @param {String} uri 
   * @param {Object} params 
   * @param {Function} prev 
   */
  Vue.prototype.$sPost = (uri, params = {}, prev = data => data) => {
    return new Promise((resolve, reject) => {
      Vue.http.post(uri, params, {
        emulateJSON: true,
        emulateHTTP: true
      }).then(response => {
        if (typeof response.body === 'string') {
          response.body = JSON.parse(response.body)
        }
        response.body = prev(response.body)
        if (verify(response.body)) {
          resolve(response.body)
        }
      }, response => {
        error(response)
        reject(response)
      })
    })
  }

  Vue.wGet = Vue.prototype.$wGet
  Vue.wPost = Vue.prototype.$wPost
  Vue.sPost = Vue.prototype.$sPost
}

/**
 * 
 * @param {Object} response 
 * @returns {Boolean} truefalse
 */
function verify (response) {
  if (response.code >= 200 && response.code <= 299) {
    return true
  }
  switch (response.code) {
    case 100:
      location.href = '/home'
      break
    case 101:
      ElementUI.Message('')
      break
    case 300:
      location.href = response.data
      break
    case 301:
      ElementUI.MessageBox.alert(response.message, '', {
        confirmButtonText: '',
        callback (action) {
          if (action === 'confirm') {
            location.href = response.data
          }
        }
      })
      break
    case 400:
      ElementUI.Message.error(response.message)
      break
    case 500:
      ElementUI.Message.error('')
      break
    case 501:
      ElementUI.Message.error('')
      break
    default:
      ElementUI.Message.error('')
      break
  }
  return false
}

/**
 * 
 */
function error (e) {
  ElementUI.Message('')
  console.error(e)
}

export default plugin



